import React, { Component } from "react";
import { withRouter } from "react-router";
import Courses from "@components/Courses";
import CoursesData from "@data/courses";
import NewCourse from "@components/NewCourse";
import DocumentSettings from "@utils/seo";

const courseSnippetCreator = () => {
    const data: any = {
        "@context": "http://schema.org",
        "@type": "ItemList",
        "itemListElement": []
    }

    const itemList = (CoursesData || []).map((course: any, index: number) => {
        return {
            "@type": "ListItem",
            "position": index + 1,
            "url": "https://www.cangokceaslan.com/course/" + course.id
        }
    })

    data["itemListElement"] = itemList;

    return data;
}

function CoursesScreen(props: { courses: Array<any> }) {
    const seo: any = {
        title: "Eğitimlerim | Can Gökçeaslan",
        description: "En son online eğitimlerime bu sayfadan ulaşabilirsiniz",
        keywords: "Udemy kurslarım, Udemy, Node.js, Video Kurs, Video Kurslarım, Udemy, Udemy JavaScript, Udemy Ücretsiz Kurslar, Node.js, NodeJs, JavaScript, Fullstack Developer, CoFounder, Girişim",
        author: "Can Gökçeaslan",
        locale: "tr",
        image: "https://www.cangokceaslan.com/assets/jpg/courses/Iyzico.jpg",
        siteName: "https://www.cangokceaslan.com/courses",
        snippetData: courseSnippetCreator()
    }
    return (
        <React.Fragment>
            <DocumentSettings docData={seo} />
            <h1 style={{ display: "none" }}>Kurslarım</h1>
            <Courses hideTitle={true} />
            <NewCourse />
        </React.Fragment>
    )
}

export default withRouter(CoursesScreen);